import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Location } from '@angular/common';

import { UserService } from '@services/modules/user/services/user.service';
import { PlatformService } from '@services/platform.service';
import { CustomerInfo } from '@shared/schemas/tz-customer-info';

@Component({
    selector: 'tz-profile',
    templateUrl: './profile.component.html',
    styleUrls: ['./profile.component.scss'],
})
export class ProfileComponent implements OnInit {
    public customerInformation!: CustomerInfo;
    public profileForm = new FormGroup({});
    public billingForm = new FormGroup({});
    public readonly page = 'PROFILE.PROFILE';
    public selectedTab: 0 | 1 = 0;

    constructor(
        private userService: UserService,
        public platformService: PlatformService,
        private location: Location
    ) {}

    get isDirty(): boolean {
        console.log(this.profileForm.dirty, this.billingForm.dirty);
        return this.profileForm.dirty || this.billingForm.dirty;
    }

    ngOnInit() {
        this.customerInformation = this.userService.getCustomerInformation();
    }

    public updateCustomerInfo(event: { customer: CustomerInfo }): void {
        if (!event) {
            return;
        }

        this.userService.updateCustomerInfo(event.customer).finally(() => {
            this.customerInformation = this.userService.getCustomerInformation();
        });
    }

    public onGoToBack(): void {
        this.location.back();
    }
}
